@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
.app {
    display: flex;
}
.sidebar {
    width: 35vw;
    background-color: var(--themeBlack);
    height: 100vh;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: start;
}
.sidebar__bannerImage {
    width: 8rem;
    padding-left: 0.7rem;
}
.sidebar__pages {
    color: var(--themeWhite);
    width: 100%;
}
.sidebar__pages__list {
    list-style: none;
    margin-top: 1.5rem;
}
.sidebar__pages__list > li {
    padding: 0.7rem 1rem;
    display: flex;
    align-items: center;;
    border-radius: 0.2rem;
}
.sidebar__pages__list > li:hover {
    background-color: var(--themeGrey);
}
.sidebar__pages__list__icon {
    margin-right: 0.4rem;
}
.sidebar__userPlaylist {
    color: var(--themeWhite);
    width: 100%;
}
.sidebar__userPlaylist__list {
    margin-top: 2rem;
    list-style: none;
}
.sidebar__userPlaylist__list > li {
    padding: 0.7rem 1rem;
    display: flex;
    align-items: center;;
    border-radius: 0.2rem;
    cursor: pointer;
}
.sidebar__userPlaylist__list > li:hover {
    background-color: var(--themeGrey);
    cursor: pointer;
}
.sidebar__divider {
    margin: 1.5rem 0;
    width: 100%;
    background-color: var(--themeWhite);
    opacity: 0.3;
}
@media screen and (min-width : 768px) {
    .sidebar {
        width: 25vw;
    }
}
@media screen and (min-width : 1200px) {
    .sidebar {
        width: 20vw;
    }
}
.content {
  background-color: var(--themeBackground);
  width: 65vw;
  height: 100vh;
  overflow: auto;
  padding-bottom: 8rem;
}
@media screen and (min-width: 768px) {
  .content {
    width: 75vw;
  }
}
@media screen and (min-width: 1200px) {
  .content {
    width: 80vw;
  }
}


.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}
.navbar__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 5.5rem;
}
.navbar__button__design {
    background-color: var(--themeBlack);
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
    border-radius: 50%;
    text-align: center;
}
.navbar__button__icon {
    color: var(--themeWhite);
    padding-left: 0.3rem !important;
    font-size: 1.5rem !important;
}

.recentMusic__tile__container {
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    grid-row-gap: 1rem;
    row-gap: 1rem;
}
@media screen and (min-width : 1200px) {
    .recentMusic__tile__container {
        grid-template-columns: auto auto auto;
    }
    .recentMusic {
        padding: 2rem;
    }
}

.contentPadding {
    padding: 1.5rem;
    color: var(--themeWhite);
}
.contentPadding > h1 {
    font-weight: 500;
}
.contentMargin {
    margin-top: 1rem;
}
.recentMusicTile {
    color: var(--themeWhite);
    display: flex;
    align-items: center;
    background-color: var(--themeBlack);
    width: 100%;
    border-radius: 0.3rem;
    height: 5rem;
    transition: 0.3s ease-in-out;
}
.recentMusicTile__image {
    width: 100%;
    height: 5rem;
    border-radius: 0.3rem 0 0 0.3rem;
}
.recentMusicTile__name__container {
    border-radius: 0% 0.3rem 0.3rem 0;
    font-size: small;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    padding: 1rem;
    background-color: var(--themeLightGrey);
    transition: 0.2s ease-in-out;
}
.recentMusicTile__name__container:hover {
    filter: brightness(140%);
    -webkit-filter: brightness(140%);
}
.recentMusicTile__name__container:hover .recentMusicTile__icon {
    visibility: visible !important;
}
.recentMusicTile__name {
    margin-left: 1rem;
}
.recentMusicTile__icon {
    visibility: hidden !important;
    width: 2.5rem !important;
    height: 2.5rem !important;
    fill: var(--themeGreen) !important;
    filter: drop-shadow(0px 5px 5px rgba(0,0,0,0.5));
    -webkit-filter: drop-shadow(0px 5px 5px rgba(0,0,0,0.5));
    transition: 0.3s ease-in-out;
}
@media screen and (min-width: 768px) {
    .recentMusicTile__image {
        width: 5.5rem;
    }
}
@media screen and (min-width : 1200px) {
    .recentMusicTile__name__container {
        font-size: large;
    }
    .recentMusicTile__image {
        width: 8rem;
    }
}
@media screen and (min-width: 1440px) {
    .recentMusicTile__image {
        width: 5.8rem;
    }
}
.musicList__Card__Container {
    display: flex;
    overflow: auto;
    align-items: center;
}
::-webkit-scrollbar {
    display: none;
}
.musicCard {
  margin: 0 0.8rem;
}
.musicCardArea {
  width: 15.5rem !important;
}

.musicCard:first-child {
  margin-left: 0 !important;
}
.musicCardCard {
  width: 15.5rem;
  padding: 0.8rem;
  background-color: var(--themeGrey) !important;
  color: var(--themeWhite) !important;
}
.musicCard__Image__Container {
  position: relative;
}
.musicCardImage {
  height: 12rem;
}
.musicCardContent {
  padding-left: 0 !important;
  padding-right: 0 !important;
  margin: 0 !important;
}
.musicCardSubtitle {
  color: var(--themeSubtitleColor);
}

.musicCard__icon {
  position: absolute;
  right: 0.5rem;
  bottom: 0.5rem;
  visibility: hidden !important;
  width: 3rem !important;
  height: 3rem !important;
  fill: var(--themeGreen) !important;
  transition: 0.3s ease-in-out !important;
  filter: drop-shadow(0px 5px 5px rgba(0, 0, 0, 0.5));
  -webkit-filter: drop-shadow(0px 5px 5px rgba(0, 0, 0, 0.5));
}
.musicCard__icon:hover {
    transform: scale(1.1);
}
.musicCardArea:hover .musicCard__icon {
  visibility: visible !important;
}

.bottom {
    background-color: var(--themeGrey);
    color: var(--themeWhite);
    display: flex;
    justify-content: stretch;
    align-items: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 6rem;
    padding: 1rem;
}
.left {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 18rem;
}
.left__image {
    width: 4rem;
    height: 4rem;
}
.left__text {
    display: flex;
    flex-direction: column;
}
.left__text > a:nth-child(1) {
    text-decoration: none;
    color: var(--themeWhite);
}
.left__text > a:nth-child(2) {
    text-decoration: none;
    color: var(--themeSubtitleColor);
}
.left__icons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 3.5rem;
}
.left__icons__icon {
    cursor: pointer;
}
@keyframes popUp {
    0% {
        transform: scale(1);
        -webkit-filter: brightness(100%);
                filter: brightness(100%);
    }
    50% {
        transform: scale(1.2);
        -webkit-filter: brightness(150%);
                filter: brightness(150%);
    }
    100% {
        transform: scale(1);
        -webkit-filter: brightness(100%);
                filter: brightness(100%);
    }
}

.middle {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    flex: 2 1;
}
.middle__upper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 20rem;
}

.middle__upper__play {
  height: 3rem !important;
  width: 3rem !important;
}

.range {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.range > p {
  padding: 0rem 0.5rem;
}
.range > input[type='range'] {
  width: 460px;
  background-color: var(--themeLightGrey);
  -webkit-appearance:none;
  border-radius: 50px;
  height: 5px;
}
.range > input[type='range']::-webkit-slider-runnable-track {
  height: 5px;
  margin-top: -1px;
  width: 5rem;
  background-color: var(--themeLightGrey);
  border-radius:10px;
}
.range > input[type='range']::-webkit-slider-thumb {
  width: 15px;
  height: 15px;
  -webkit-appearance:none;
  background: var(--themeGreen);
  border-radius: 50%;
  margin-top: -4px;
}
.right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 18rem;
}
.login {
    width: 100%;
    height: 100vh;
    background-color: var(--themeBlack);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.login__image {
    width: 30rem;
    margin-bottom: 4rem;
}
.login__button {
    background-color: var(--themeGreen) !important;
    border: none;
    color: var(--themeWhite);
    padding: 1rem 8rem;
    font-size: 1.5rem;
    border-radius: 0.5rem;
    transition: 0.3s ease-in-out;
}
.login__button:hover {
    background-color: var(--themeDarkGreen) !important;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
:root {
  --themeBlack : #000000;
  --themeSecondaryBlack : #101010;
  --themeGrey : #1D1D1D;
  --themeGreen : #64D762;
  --themeDarkGreen : #50b04e;
  --themeBlue : #2E77D0;
  --themeWhite : #ffffff;
  --themeLightGrey : #404A4C;
  --themeBackground : #131313;
  --themeSubtitleColor: #ABABAB;
}
